* {
  box-sizing: border-box;
  font-family: "Domine", serif;
  background: rgb(0, 0, 0);
  /*   background: linear-gradient(117deg, rgba(0,0,0,1) 0%, rgba(0,0,0,1) 20%, rgba(0,0,0,1) 41%, rgba(0,0,0,1) 59%, rgba(13,13,14,1) 85%); */
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4rem;
  color: #fff;
}

.coin-search {
  margin-bottom: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 2rem;
  text-align: center;
  font-size: 2em;
  font-weight: bolder;
  position: relative;
  top: 0.625rem;
}

.coin-input {
  padding-left: 1rem;
  width: 25rem;
  height: 3.125rem;
  border-radius: 0.375rem;
  border: none;
  outline: none;
  font-size: 1rem;
  background-image: linear-gradient(
    -225deg,
    #2711ee 0%,
    #18c6f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
}

.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.coin-input::placeholder {
  color: #e2e2e2;
}

a:hover {
  color: #18c6f2;
  transition: all 0.3s ease;
}

a:hover {
  cursor: pointer;
}

.navbar {
  clear: both;
}

.scroll-to-top {
  position: fixed;
  width: 5rem;
  bottom: 0.5rem;
  left: 96%;
  -webkit-animation: fadeIn 700ms ease-in-out 1s both;
          animation: fadeIn 700ms ease-in-out 1s both;
  cursor: pointer;
}

@media (max-width: 767px) {
  .scroll-to-top {
    left: 90%;
  }
}


@media screen and (max-width:480px) and (min-width:320px)  {
 .scroll-to-top{
   left: 63%;
 }
}

