.App-converter {
  height: 30rem;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 10rem;
  background-color: black;
}
.heading-converter {
  margin-top: -9rem;
  font-size: 35px;
}
.container-converter {
  height: 300px;
  width: 800px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.convimg {
  width: 500px;
}
input {
  padding-left: 5px;
  font-size: 20px;
  font-weight: bold;
  height: 50px;
  background-color: cyan;
  color: black;
  font-size: 1.3rem;
  font-weight: bold;
}
.middle,
.right {
  width: 120px;
  font-size: 1.3rem;
  margin-top: -0.9rem;
  text-transform: capitalize;
  font-weight: bold;
  text-align: center;
}
.switch {
  padding: 5px;
  margin-top: 25px;
  background-color: black;
  border-radius: 50%;
  cursor: pointer;
}
.result-converter {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 800px;
  padding-left: 30px;
  text-transform: capitalize;
  align-items: center;
  align-content: center;
  justify-content: center;
  line-height: 3rem;
}
.button-conv {
  display: flex;
  width: 100px;
  height: 30px;
  font-weight: bold;
  font-size: 20px;
  border: 2px solid cyan;
  background-color: cyan;
  cursor: pointer;
  color: black;
  align-items: center;
  justify-content: center;
  align-content: center;
  margin-bottom: 2rem;
  border-radius: 10%;
}

.Dropdown-control {
  background-color: cyan;
}
.Dropdown-placeholder {
  background-color: cyan;
  color: black;
  text-transform: uppercase;
}

.Dropdown-option {
  background-color: cyan;
  color: black;
  text-transform: uppercase;
}
.resize{
  height: 2.7rem;
  padding-bottom: 0rem;
}

.amounthdr{
  text-align: center;
  font-size: 1.3rem;
  margin-top: -0.5rem;
}



